`, "",{"version":3,"sources":["webpack://./src/views/ViewPopup.vue"],"names":[],"mappings":";AAgGA;EACE,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,6BAA6B;EAC7B,mBAAmB;AACrB","sourcesContent":["<template>\n  <div class=\"Popup\">\n    <DpButton\n      width=\"50%\"\n      @click=\"() => showPop('top')\"\n    >\n      Top\n    </DpButton>\n    <DpButton\n      width=\"50%\"\n      @click=\"() => showPop('right')\"\n    >\n      Right\n    </DpButton>\n    <DpButton\n      width=\"50%\"\n      @click=\"() => showPop('bottom')\"\n    >\n      Bottom\n    </DpButton>\n    <DpButton\n      width=\"50%\"\n      @click=\"() => showPop('left')\"\n    >\n      Left\n    </DpButton>\n\n    <Popup\n      v-model=\"show\"\n      :position=\"side\"\n      :width=\"width\"\n      :height=\"height\"\n      :show-mask=\"true\"\n    />\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { defineComponent, ref, nextTick } from 'vue'\n// import type { Side } from './dist/types/components/popup/types'\nimport type { PopupProps } from '@/components/popup/types'\ntype Side = 'top' | 'right' | 'bottom' | 'left'\n\nexport default defineComponent({\n  name: 'ViewPopup',\n  setup () {\n    const side = ref<PopupProps['position']>('top')\n    const show = ref<PopupProps['modelValue']>(false)\n    const width = ref<PopupProps['width']>('100%')\n    const height = ref<PopupProps['height']>('100%')\n    const sideList: Side[] = ['top', 'right', 'bottom', 'left']\n\n    const showPop = (s: Side) => {\n      show.value = false\n      nextTick(() => {\n        if (!sideList.includes(s)) s = 'top'\n\n        switch (s) {\n        case 'top':\n          width.value = '100%'\n          height.value = 0\n          break\n        case 'right':\n          width.value = '70%'\n          height.value = '100%'\n          break\n        case 'bottom':\n          width.value = '100%'\n          height.value = '70%'\n          break\n        case 'left':\n          width.value = '70%'\n          height.value = '100%'\n          break\n        }\n\n        side.value = s\n\n        setTimeout(() => {\n          show.value = true\n        }, 350)\n      })\n    }\n\n    return {\n      side,\n      show,\n      width,\n      height,\n      showPop\n    }\n  }\n})\n</script>\n\n<style scoped>\n.Popup {\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-around;\n  align-items: center;\n}\n</style>\n"],"sourceRoot":""}]);
